<template>
  <div class="userInfo">
    <PublicHeader>个人信息</PublicHeader>
    <div class="content" v-if="login">
      <ul>
        <li>
          <div class="box">
            <h3>头像</h3>
            <div class="right">
              <img :src="avator" alt="">
              <input type="file" ref="file" class="file">
              <i class="iconfont">&#xe610;</i>
            </div>
          </div>
        </li>
        <li>
          <div class="box">
            <h3>昵称</h3>
            <div class="right">
              <input type="text" v-model="userName" class="userName text">
              <i class="iconfont">&#xe610;</i>
            </div>
          </div>
        </li>
        <li>
          <div class="box">
            <h3>性别</h3>
            <div class="right">
              <select v-model="gender" class="text">
                <option value="未知">未知</option>
                <option value="男">男</option>
                <option value="女">女</option>
              </select>
            <i class="iconfont">&#xe610;</i>
            </div>
          </div>
        </li>
        <li class="email">
          <div class="box">
            <h3>邮箱</h3>
            <div class="right"><span v-if="email" class="text">{{email}}</span><span v-else class="text">去绑定</span><i class="iconfont">&#xe610;</i>
            </div>
          </div>
        </li>
        <li class="intro">
          <div class="box">
            <h3>简介</h3>
          </div>
          <textarea placeholder="介绍一下自己吧" v-model="intro"></textarea>
        </li>
      </ul>
      <div class="save" @click="save"><span>保存</span></div>
    </div>
    <NoLogin v-else></NoLogin>
  </div>
</template>
<script>
import PublicHeader from 'components/PublicHeader'
import NoLogin from 'components/NoLogin'
import { modifyUser } from 'api/api'
import { modifyLocalStorage } from 'utils/handleData'
export default {
  components: { PublicHeader, NoLogin },
  data () {
    return {
      login: false,
      avator: '',
      userName: '',
      userNameOld: '',
      intro: '',
      email: '',
      gender: ''
    }
  },
  methods: {
    save () {
      const file = this.$refs.file.files[0]
      const data = new FormData()
      if (file) {
        data.append('img', file)
      }
      data.append('userName', this.userName)
      data.append('userNameOld', this.userNameOld)
      data.append('intro', this.intro)
      data.append('email', this.email)
      data.append('gender', this.gender)
      modifyUser(data)
        .then((res) => {
          modifyLocalStorage(true, res.data)
          this.userNameOld = res.data.userName
          alert('保存成功')
          this.$router.go(0)
        })
    }
  },
  mounted () {
    let userInfo = localStorage.getItem('userInfo')
    if (!userInfo) { return false }
    userInfo = JSON.parse(userInfo)
    if (!userInfo.state) { this.login = false; return false }
    if (userInfo.state) {
      this.login = true
      this.userName = userInfo.userName
      this.userNameOld = userInfo.userName
      this.avator = this.baseUrl + userInfo.avator
      this.intro = userInfo.intro
      this.email = userInfo.email
      this.gender = userInfo.gender
    }
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.userInfo{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: #f8f8f8;
  .content{
    position: absolute;
    top: .88rem;
    left: 0;
    right: 0;
  }
  ul{
    margin-top: 1px;
    li{
      margin-top: 1px;
      background: #fff;
      .box{
        padding: .2rem .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .right{
          display: flex;
          align-items: center;
          position: relative;
          img{
            width: 1.2rem;
            height: 1.2rem;
            border-radius: 50%;
          }
          .userName{
            text-align: right;
          };
          .file{
            opacity: 0;
            position: absolute;
            right: 0;
            top: 0;
            left: 0;
            bottom: 0;
          }
        }
        h3{
          font-size: .3rem;
          line-height: .60rem;
          color: #191919;
        }
        .iconfont{
          font-size: .3rem;
          line-height: .60rem;
          color: #b2b2b2;
          margin-left: 0.37rem;
        }
        .text{
          font-size: .28rem;
          line-height: .42rem;
          color: #7f7f7f;
        }
        select{
          text-align: right;
          background: transparent;
        }
      }
    }
    .email{
      margin: .16rem 0;;
    }
    .intro{
      height: 4.14rem;
      display: flex;
      flex-direction: column;
      textarea{
        flex: 1;
        padding: 0 .3rem;
        overflow-y: scroll;
        font-size: .28rem;
        line-height: .42rem;
      }
    }
  }
  .save{
    width: 4.52rem;
    height: 0.9rem;
    margin: .92rem auto 0;
    border-radius: .5rem;
    text-align: center;
    line-height: 0.9rem;
    color: #fff;
    font-weight: bold;
    font-size: .3rem;
    letter-spacing: .08rem;
    background: linear-gradient(90deg, @orange 0%, @orange2 100%);
  }
}
</style>
